import React from "react";
import * as animaciones from 'react-animations';
import styled, { keyframes } from 'styled-components';

const defaultProps = {
	config : {
	  animacion: "bounce",
	  tiempo: "2s",
	  clase:""
	}
};

function Animate(props){
	let  {animacion,tiempo,clase,id}  = props.config
	const bounceAnimation = keyframes`${animaciones[animacion]}`;
	const WrapperDiv = styled.div`
	  animation: ${tiempo} ${bounceAnimation};
	`;

	return(
			<WrapperDiv className={clase}>
				{props.children}
			</WrapperDiv>
	)
}

Animate.defaultProps = defaultProps;
export default Animate;